:local(.navbarBackground) {
    /* margin: 1rem auto; */
    margin: auto;
    position: relative;
    -o-transition: 1.5s ease;
    transition: 1.5s ease;
    -webkit-transition: 1.5s ease;
    background-color: #ABD7D8;
    background: -webkit-gradient(linear,left top, left bottom,from(rgba(153, 221, 238, 0.6)),to(rgba(0, 0, 0, 0.1)));
    background: -webkit-linear-gradient(rgba(153, 221, 238, 0.6),rgba(0, 0, 0, 0.1));
    background: -o-linear-gradient(rgba(153, 221, 238, 0.6),rgba(0, 0, 0, 0.1));
    background: linear-gradient(rgba(153, 221, 238, 0.6),rgba(0, 0, 0, 0.1));
    /* background-color: #C1A9D4; */
  }


